.theme-btn {

    width: 3em;
    height: fit-content;
    fill: var(--button-bg-color);
    transition: fill 0.5s ease-in-out;
    padding: 0;
    padding-block: 4px;

}

.theme-btn:hover {
    cursor: pointer;
    text-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
    fill: var(--hover-bg-color);
}